</style>
<script>
  import apiService from '../../../util/apiService'
  import _ from 'lodash'
  import moment from 'moment'
  import {dataBase} from  '../../../firebase/vueFire'
  import fireBase from '../../../firebase/vueFire'
  import { isInApp } from '../../../util/util'
  var deals = dataBase.ref()
  var ratingRef = deals.child('bargains')
  var cc = 0
  export default {
    name: 'AllBargains',
    props: {
      param: {
        type: Object,
        default: () => {
        }
      }
    },
    data (){
      return {
        busy: false,
        distance: 200,
        previousRequest: null,
        opacity: {
          opacity: '0.2'
        },
        isInApp: isInApp,
        page: 1,
        limit: 30,
        params: {},
        rating: {},
        isEmpty: false,
        ratingObj: {},
        totalPages: 0,
        selected: this.$parent.selected,
        deals: [],
        loading: true,
      }
    },
    computed: {
      storeList () {
        return this.$store.getters.getObj
      }
    },
    methods: {
      getLabelColor(label) {
        if (label === 'Expired') {
          return 'badge badge-danger'
        } else if (status === 'Out of Stock') {
          return 'badge badge-warning'
        }
      },
      getStatus (status) {
        if (status === 'expired') {
          return 'Expired'
        } else if (status === 'outOfStock') {
          return 'Out of Stock'
        }
      },
      isExpiredBool (deal) {
        if (deal.bargainStatus === 'Expired' || deal.bargainStatus === 'Out of Stock') {
          return true
        } else {
          return false
        }
      },
      isExpired (deal) {
        if (deal.bargainStatus === 'Expired' || deal.bargainStatus === 'Out of Stock') {
          return 'b-exp dark-white'
        }
      },
      getAuthor (deal) {
        if (deal.authorType === 'internal') {
          return './static/mini-logo.svg'
        } else {
          return 'https://cdn1.iconfinder.com/data/icons/business-12/6144/1-24.png'
        }
      },
      isInternal (deal) {
        if (deal.authorType === 'internal') {
          return false
        } else {
          return true
        }
      },
      paramsFilter () {
        if (_.isEmpty(this.params.keywords)) {
          delete this.params['keywords']
        }
        if (_.isEmpty(this.params.categories)) {
          delete this.params['categories']
        }
        if (_.isEmpty(this.params.type)) {
          delete this.params['type']
        }
        if (this.params.maxSalePrice === null || this.params.maxSalePrice === undefined) {
          delete this.params['maxSalePrice']
        }
        if (this.params.minSalePrice === null || this.params.minSalePrice === undefined) {
          delete this.params['minSalePrice']
        }
      },
      getPath (deal) {
        return 'https://bestbargains-dev.firebaseapp.com/detail/' + deal._id + '/' + deal.generateTitle
      },
      getDateAdded (date) {
        return moment(date).fromNow()
      },
      getSalePrice (deal) {
        if (deal.type === 'sale') {
          return '$' + deal.salePrice.toFixed(2)
        } else {
          return ''
        }
      },
      truncateTitle (title) {
        return _.truncate(title, {length: 75})
      },
      getListPrice (deal) {
        if (deal.type === 'sale') {
          return '$' + deal.listPrice.toFixed(2)
        } else {
          return ''
        }
      },
      getType (deal) {
        if (deal.type === 'sale') {
          return '(' + Math.round(((deal.listPrice - deal.salePrice) / deal.listPrice) * 100) + '% off)'
        } else if (deal.type === 'percentOff') {
          return deal.percentOff + '% off'
        } else {
          return deal.type
        }
      },
      getColorTag (deal) {
        if (deal.type === 'sale') {
          if (Math.round(((deal.listPrice - deal.salePrice) / deal.listPrice) * 100) >= 40) {
            return 'red-price-text'
          } else {
            return 'green-price-text'
          }
        } else if (deal.type === 'percentOff') {
          if (deal.percentOff >= 40) {
            return 'red-price-text'
          } else {
            return 'green-price-text'
          }
        } else {
          return 'green-price-text'
        }
      },
      dateAdded: function (date) {
        return 'Posted on ' + moment(date).format('MM-DD-YYYY') + ' at ' + moment(date).format('hh:mm A')
      },
      isSelected: function (id) {
        if (id) {
          return 'active'
        } else {
          return ''
        }
      },
      getRating: function (deal) {
      },
      upVote (deal) {
        var o = deal
        deal = deal.id
        if (this.$store.getters.getToken === null) {
          this.$root.$refs.toastr.Add({
            title: "",
            msg: 'You need to be Logged in before you can vote!',
            clickClose: true,
            timeout: 4000,
            position: "toast-top-right",
            type: "error"
          })
        } else {
          var userID = this.$store.getters.getUser.id
          ratingRef.child(deal).child('likes').child(userID).once('value').then((userValue) => {
            var isExist = userValue.val()
            if (isExist !== null && isExist !== undefined) {
              ratingRef.child(deal).child('likes').child(userID).remove().then(() => {
                o.set = false
              })
              deals.child('users').child(userID).child('likes').child(deal).remove().catch(() => {
                this.Denied()
              })
            } else {
              ratingRef.child(deal).child('likes').child(userID).set(new Date().getTime()).then(() => {
                o.set = true
              }).catch(() => {
                this.Denied()
              })
              deals.child('users').child(userID).child('likes').child(deal).set(new Date().getTime())
            }
          })
        }
      },
      Denied () {
        this.$root.$refs.toastr.Add({
          title: "",
          msg: 'Permission Denied',
          clickClose: true,
          timeout: 2000,
          position: "toast-top-center",
          type: "error"
        })
      },
      bookMark (deal) {
        var o = deal
        deal = deal.id
        if (this.$store.getters.getToken === null) {
          this.$root.$refs.toastr.Add({
            title: "",
            msg: 'You need to be logged in before you can bookmark a bargain!',
            clickClose: true,
            timeout: 4000,
            position: "toast-top-right",
            type: "error"
          })
        } else {
          var userID = this.$store.getters.getUser.id
          deals.child('users').child(userID).child('favorites').child(deal).once('value').then((userValue) => {
            var isExist = userValue.val()
            if (isExist !== null && isExist !== undefined) {
              deals.child('users').child(userID).child('favorites').child(deal).remove().then(() => {
                o.fav = false
              }).catch(() => {
                this.Denied()
              })
              delete this.deals[deal]
              if (_.isEmpty(this.deals)) {
                this.isEmpty = true
              }
            } else {
              deals.child('users').child(userID).child('favorites').child(deal).set(new Date().getTime()).then(() => {
                o.fav = true
              }).catch(() => {
                this.Denied()
              })
            }
          })
        }
      },
      getImage: function (image) {
        var value = 'https://files.slack.com/files-tmb/T2V9T5KHB-F3VV8THNV-28a9b2cb7f/pasted_image_at_2017_01_25_06_26_pm_480.png'
        if (image !== undefined) {
          if (image[0] !== undefined) {
            value = image[0].url
          }
        }
        return value || ''
      },
      getPrice: function (deal) {
        if (deal.type === 'sale') {
          return '$' + deal.salePrice
        } else if (deal.type === 'percentOff') {
          return deal.percentOff + '% Off'
        } else {
          return deal.type
        }
      },
      generateTitle: function (title) {
        if (title !== undefined) {
          return title.replace(/[^A-Za-z0-9]/g, ' ').replace(/\s\s+/g, ' ').trim().replace(/ /g, "-")
        } else {
          return ''
        }
      },
      getStoreName: function (id) {
        var obj = ''
        if (!_.isEmpty(this.storeList[id])) {
          obj = this.storeList[id].imageUrl;
        } else {
          obj = 'N/A'
        }
        return obj
      },
      getDeals () {
        this.init()
      },
      getTag: function (deal) {
        if (deal.type === 'sale') {
          if (deal.salePrice / deal.listPrice >= 30) {
            return 'red'
          } else if (deal.salePrice / deal.listPrice >= 10) {
            return 'green'
          } else if (deal.salePrice / deal.listPrice >= 0) {
            return 'blue'
          } else {
            return 'blue'
          }
        } else if (deal.type === 'percentOff') {
          if (deal.percentOff >= 30) {
            return 'red'
          } else if (deal.percentOff > 10) {
            return 'green'
          } else if (deal.percentOff >= 0) {
            return 'blue'
          } else {
            return 'blue'
          }
        } else if (deal.type === 'free') {
          return 'red'
        } else {
          return 'green'
        }
      },
      loadMore: function () {
        if (this.page <= this.totalPages) {
          this.params.limit = this.limit
          this.params.page = this.page
          deals.child('users').child(this.$store.getters.getUser.id).child('favorites').once('value').then((s) => {
            this.params.bargainIds = _.keys(s.val())
            if (!_.isEmpty(this.params.bargainIds)) {
              apiService.getSelectedBargains(this.params, this.previousRequest).then((response) => {
                this.page += 1
                this.loading = false
                this.isEmpty = false
                this.opacity.opacity = '1'
                var obj = response.body.docs
                _.map(obj, (o) => {
                  if (_.isEmpty(this.deals[o._id])) {
                    this.$set(this.deals, o._id, o)
                    this.$set(o, 'fireRating', 0)
                    this.$set(o, 'set', false)
                    o.generateTitle = this.generateTitle(o.title)
                    o.off = this.getType(o)
                    o.getAuthor = this.getAuthor(o)
                    o.datePublished = this.getDateAdded(o.datePublished)
                    o.title = this.truncateTitle(o.title)
                    o.shareTitle = (!_.isEmpty(this.getSalePrice(o))) ? this.getSalePrice(o) + ' - ' : ''
                    o.shareTitle += o.title
                    o.generateListPrice = this.getListPrice(o)
                    o.generateSalePrice = this.getSalePrice(o)
                    o.images = this.getImage(o.images)
                    o.bargainStatus = this.getStatus(o.bargainStatus)
                    ratingRef.child(o._id).child('likes').once('value').then((snapshot) => {
                      o.fireRating = snapshot.numChildren()
                      if (this.$store.getters.isAuth) {
                        ratingRef.child(o._id).child('likes').child(this.$store.getters.getUser.id).once('value').then((response) => {
                          response = response.val()
                          if (response !== null && response !== undefined) {
                            o.set = true
                          } else {
                            o.set = false
                          }
                        })
                      } else {
                        if (0 !== null && 0 !== undefined) {
                          o.set = false
                        }
                      }
                    })
                    return o._id
                  }
                })
                this.$refs.infiniteLoading.$emit('$InfiniteLoading:loaded')
              }, () => {
                this.loading = false
                this.opacity.opacity = '1'
                this.$refs.infiniteLoading.$emit('$InfiniteLoading:loaded')
              })
            } else {
              this.loading = false
              this.isEmpty = true
            }
          })
        } else {
          this.$refs.infiniteLoading.$emit('$InfiniteLoading:complete')
        }
      },
      onScroll: function (n) {
      },
      init () {
        this.paramsFilter()
        this.params.limit = this.limit
        this.params.page = 1
        this.page = 1
        deals.child('users').child(this.$store.getters.getUser.id).child('favorites').once('value').then((s) => {
          this.params.bargainIds = _.keys(s.val())
          if (!_.isEmpty(this.params.bargainIds)) {
            apiService.getSelectedBargains(this.params, this.previousRequest).then((response) => {
              this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset')
              this.loading = false
              this.isEmpty = false
              this.opacity.opacity = '1'
              this.totalPages = response.body.pages
              this.deals = _.keyBy(response.body.docs, (o) => {
                this.$set(o, 'fireRating', 0)
                this.$set(o, 'set', false)
                this.$set(o, 'fav', false)
                o.generateTitle = this.generateTitle(o.title)
                o.off = this.getType(o)
                o.getAuthor = this.getAuthor(o)
                o.datePublished = this.getDateAdded(o.datePublished)
                o.title = this.truncateTitle(o.title)
                o.shareTitle = (!_.isEmpty(this.getSalePrice(o))) ? this.getSalePrice(o) + ' - ' : ''
                o.shareTitle += o.title
                o.generateListPrice = this.getListPrice(o)
                o.generateSalePrice = this.getSalePrice(o)
                o.images = this.getImage(o.images)
                o.bargainStatus = this.getStatus(o.bargainStatus)
                ratingRef.child(o._id).child('likes').once('value').then((snapshot) => {
                  o.fireRating = snapshot.numChildren()
                  if (this.$store.getters.isAuth) {
                    ratingRef.child(o._id).child('likes').child(this.$store.getters.getUser.id).once('value').then((response) => {
                      response = response.val()
                      if (response !== null && response !== undefined) {
                        o.set = true
                      } else {
                        o.set = false
                      }
                    })

                    deals.child('users').child(this.$store.getters.getUser.id).child('favorites').child(o._id).once('value').then((response) => {
                      response = response.val()
                      if (response !== null && response !== undefined) {
                        o.fav = true
                      } else {
                        o.fav = false
                      }
                    })

                  } else {
                    if (0 !== null && 0 !== undefined) {
                      o.set = false
                    }
                  }
                })
                return o._id
              })

              if (this.$store.getters.isAuth) {
                var fav = deals.child('users').child(this.$store.getters.getUser.id).child('favorites')
                fav.on('child_added', (snap) => {
                  if (!_.isEmpty(this.deals[snap.key])) {
                    this.deals[snap.key].fav = true
                  }
                })
                fav.on('child_removed', (snap) => {
                  this.deals[snap.key].fav = false
                })
              }

              ratingRef.on('child_added', (snap) => {
                if (!_.isEmpty(this.deals[snap.key])) {
                  this.deals[snap.key].fireRating = snap.child('likes').numChildren() || 0
                }
              })
              ratingRef.on('child_removed', (snap) => {
                var count = snap.child('likes').numChildren()
                this.deals[snap.key].fireRating = count || 0
                if (count === 1) {
                  this.deals[snap.key].fireRating = 0
                }
              })
              ratingRef.on('child_changed', (snap) => {
                this.deals[snap.key].fireRating = snap.child('likes').numChildren() || 0
              })
            }, () => {
              this.loading = false
              this.opacity.opacity = '1'
            })
          } else {
            this.loading = false
            this.isEmpty = true
          }
        })
      }
    },
    beforeRouteLeave (to, from, next) {
      ratingRef.off()
      deals.off()
      next()
    },
    mounted () {
      $('[data-toggle="tooltip"]').tooltip()
      this.params = _.cloneDeep(this.param)
      this.init()
      this.$watch('param', function (oldVal, newVal) {
        this.params = _.cloneDeep(newVal)
        this.getDeals()
      }, {deep: true})
    }
  }
</script>



